import React, { Component } from 'react'
import * as THREE from 'three'

let color_array = [
    "#1f78b4",
    "#b2df8a",
    "#33a02c",
    "#fb9a99",
    "#e31a1c",
    "#fdbf6f",
    "#ff7f00",
    "#6a3d9a",
    "#cab2d6",
    "#ffff99"
  ]

class Scene extends Component {
  constructor(props) {
    super(props)

    this.start = this.start.bind(this)
    this.stop = this.stop.bind(this)
    this.animate = this.animate.bind(this)

     
    this.state = {
        // data: this.props.data
    }
  }
  componentDidUpdate(prevProps, prevState) {
    console.log("showing both values",prevProps.data, this.props.data)
    if (this.props.data !== prevProps.data ) {
        console.log("things are different")
        // this.clearScene()
        // console.log("clearing ", this.props.data)
        // this.addPoints()
    }
   }

  componentDidMount() {
    const width = this.mount.clientWidth
    const height = this.mount.clientHeight

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      1000
    )
    const renderer = new THREE.WebGLRenderer({ antialias: true })
    const geometry = new THREE.BoxGeometry(1, 1, 1)
    const material = new THREE.MeshBasicMaterial({ color: '#433F81' })
    const cube = new THREE.Mesh(geometry, material)

    camera.position.z = 4
    // scene.add(cube)
    renderer.setClearColor('#000000')
    renderer.setSize(width, height)

    this.scene = scene
    this.camera = camera
    this.renderer = renderer
    this.material = material
    this.cube = cube

    this.mount.appendChild(this.renderer.domElement)
    this.start()
    this.addPoints()
  }

  clearScene(){
    this.scene.remove.apply(this.scene, this.scene.children);
  }

  addPoints(){
    

    for (let i = 0; i < 10; i++){
        let color = new THREE.Color(color_array[ Math.round( Math.random() * color_array.length) ] ); 
        var material = new THREE.MeshBasicMaterial( { color: color } );
        var geometry = new THREE.CircleGeometry (Math.random(), 42 );
        var circle = new THREE.Mesh( geometry, material ); 
        circle.position.setX(-5+i) 
        circle.position.setY(Math.random()) 
        this.scene.add( circle );
    }
    
  }

  componentWillUnmount() {
    this.stop()
    this.mount.removeChild(this.renderer.domElement)
  }

  start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }

  stop() {
    cancelAnimationFrame(this.frameId)
  }

  animate() {
    this.cube.rotation.x += 0.01
    this.cube.rotation.y += 0.01

    this.renderScene()
    this.frameId = window.requestAnimationFrame(this.animate)
  }

  renderScene() {
    this.renderer.render(this.scene, this.camera)
  }

  render() {
    return (
      <div
        style={{ width: '100%', height: '400px' }}
        ref={(mount) => { this.mount = mount }}
      />
    )
  }
}

export default Scene